.profile__info {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  align-items: baseline;
  max-width: 336px;
  margin-left: 30px;
}

@media screen and (max-width: 525px) {
  .profile__info {
    margin-left: 0;
    max-width: 282px;
    justify-content: center;
  }
}